import styled from "@emotion/styled";
import { Box } from "@mui/material";

export const StyledBox = styled(Box)`
  width: 95%;
  max-width: 400px;
  margin: 20px auto;
  padding: 10px;
  border: 1px solid rgb(88, 88, 220);
  border-radius: 10px;
  box-shadow: 0px 0px 10px 20px rgba(255, 255, 255, 0.2);
`;
